import React, { PureComponent, cloneElement, createElement } from 'react';
import classNames from "classnames";
import { TransitionMotion } from "../Motion/react-motion";
import { RouteTransitionProps } from "./PropsType";

export default class RouteTransition extends PureComponent<
  RouteTransitionProps,
  any
> {
  static defaultProps = {
    prefixCls: "bm-RouteTransition",
    wrapperComponent: "div",
  };

  getDefaultStyles() {
    const { children, enterStyles }: any = this.props;
    if (!children) {
      return [];
    }
    return [
      {
        key: children.key || `transition_${Math.floor(Math.random() * 100)}`,
        data: children!,
        style: enterStyles!,
      },
    ];
  }

  getStyles() {
    const { children, activeStyles }: any = this.props;
    if (!children) {
      return [];
    }
    return [
      {
        key: children.key || `transition_${Math.floor(Math.random() * 100)}`,
        data: children!,
        style: activeStyles!,
      },
    ];
  }

  willEnter = () => {
    return this.props.enterStyles!;
  };

  willLeave = (): any => {
    return { ...this.props.leaveStyles! };
  };

  didLeave = (styleThatLeft: object) => {
    if (this.props.didLeave) {
      this.props.didLeave(styleThatLeft);
    }
  };

  renderElement = (config: any = {}) => {
    const styles: any = this.props.styles;
    const props: any = {
      style: styles(config.style)!,
      key: config.key,
    };
    return !!this.props.wrapperComponent !== false
      ? createElement(this.props.wrapperComponent, props, config.data)
      : cloneElement(config.data, props);
  };

  render() {
    const { className, transitionName, prefixCls } = this.props;
    const styleClass = classNames(prefixCls, className, transitionName);
    return (
      <TransitionMotion
        defaultStyles={this.getDefaultStyles()}
        styles={this.getStyles()}
        willEnter={this.willEnter}
        willLeave={this.willLeave}
      >
        {(styles: any) => (
          <div className={styleClass}>{styles.map(this.renderElement)}</div>
        )}
      </TransitionMotion>
    );
  }
}
