import React, { Children, useState, ReactElement, cloneElement } from "react";
import classNames from "classnames";
import { AccordionGroupProps } from "./PropsType";

export default function AccordionGroup(props: AccordionGroupProps) {
  const {
    children: childrenProp,
    className,
    closeOthers,
    activeIndex = [],
    disableRipple = true,
    onChange,
    prefixCls = "bm-AccordionGroup",
  } = props;
  const styleClass = classNames(prefixCls, className);
  const [currentValue, setCurrentValue] = useState(
    Array.isArray(activeIndex) ? activeIndex : [activeIndex]
  );

  const getCurrentValue = () => {
    let values = currentValue || [];
    const accordion = props.closeOthers;

    if (!Array.isArray(currentValue)) {
      values = [currentValue];
    }

    if (accordion && currentValue.length > 1) {
      values = [currentValue[currentValue.length - 1]];
    }
    return values;
  };

  const handleChange = (event: any) => {
    const index: number = event.index;
    const expanded = event.expanded;
    let values: any = [...currentValue] || [];

    if (closeOthers) {
      values = expanded ? [] : [index];
    } else if (expanded) {
      values = currentValue.filter((value) => {
        return index !== value;
      });
    } else {
      values.push(index);
    }
    setCurrentValue(values);
    if (onChange) {
      onChange(values);
    }
  };

  const children = Children.map(
    childrenProp,
    (child: ReactElement<any>, index: number) => {
      const { disabled, name } = child.props;
      const expanded = getCurrentValue().indexOf(name || index) > -1;
      return cloneElement(child, {
        closeOthers,
        disableRipple,
        disabled,
        expanded,
        name,
        index,
        onChange: handleChange,
      });
    }
  );

  return <div className={styleClass}>{children}</div>;
}
