import React, { useEffect, useState } from "react";
import classNames from "classnames";
import { ViewProps, ViewState } from "./PropsType";

export default function View(props: ViewProps) {
  const { className, overlayBackground = "transparent", children } = props;

  const toggleSidebar = () => {
    if (props.onClose) {
      props.onClose();
    }
  };

  return (
    <div
      className={classNames("View", className)}
      style={{
        background: overlayBackground,
      }}
    >
      {children}
      <div className="View-overlay" onClick={toggleSidebar} />
    </div>
  );
}
