import React, { Fragment, useEffect, useState } from "react";
import classNames from "classnames";
import { Collapse } from "../Transitions";
import Ripple from "../Ripple";
import { AccordionProps } from "./PropsType";

export default function Accordion(props: AccordionProps) {
  const {
    children,
    className,
    disableRipple,
    header,
    prefixCls = "bm-Accordion",
    index,
    name,
    disabled,
    onChange,
  } = props;
  const [expanded, setExpanded] = useState(!!props.expanded);
  const styleClass = classNames(
    prefixCls,
    {
      [`${prefixCls}-expanded`]: expanded,
    },
    className
  );

  const handleToggle = (event: any) => {
    if (disabled) {
      return;
    }
    if (onChange) {
      onChange({
        event,
        index: name || index,
        expanded,
      });
    }
  };

  useEffect(() => {
    setExpanded(!!props.expanded);
  }, [props?.expanded]);

  return (
    <div className={styleClass}>
      <div className="bm-AccordionHeader" onClick={handleToggle}>
        <div className="bm-AccordionHeader-toggle">{header}</div>
        {!disableRipple ? <Ripple hidden={false} /> : null}
      </div>
      <Collapse in={expanded} className="bm-AccordionContent">
        <Fragment>
          <div className="bm-AccordionContentInner">{children}</div>
        </Fragment>
      </Collapse>
    </div>
  );
}
